.contacts__container {
  display: flex;
  padding-top: 83px;
  padding-bottom: 102px;
  gap: 0 135px;

  @include vp-1199 {
    flex-direction: column;
    padding-top: 88px;
    padding-bottom: 69px;
    gap: 40px 0;
  }

  @include vp-767 {
    padding-top: 67px;
    padding-bottom: 29px;
    gap: 30px 0;
  }
}

.contacts__title {
  margin: 0 0 23px;

  @include vp-1199 {
    margin: 0 0 18px;
  }

  @include vp-767 {
    margin: 0 0 11px;
  }
}

.contacts__body-text {
  margin: 0 0 49px;

  @include vp-1199 {
    max-width: 71%;
    margin: 0 0 40px;
  }

  @include vp-767 {
    max-width: 95%;
    margin: 0 0 30px;
  }
}

.contacts__body-text p {
  margin-top: 0;

  @include vp-767 {
    font-size: 14px;
    line-height: 20px;
  }
}

.contacts__body-text > *:last-child {
  margin: 0;
}

.contacts__list {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin: 0;
  padding: 0;
  list-style-type: none;
  gap: 34px 0;

  @include vp-1199 {
    flex-direction: row;
    flex-wrap: wrap;
    gap: 0 44px;
  }

  @include vp-767 {
    flex-direction: column;
    gap: 30px 0;
  }
}

.contacts__item {
  position: relative;
  display: flex;
  flex-direction: column;
  padding-left: 56px;

  svg {
    position: absolute;
    top: 12px;
    left: 0;
  }

  @include vp-1199 {
    padding-left: 34px;

    svg {
      top: -1px;
      width: 24px;
      height: 24px;
    }
  }

  @include vp-767 {
    padding-right: 70px;
    padding-left: 32px;

    svg {
      top: 0;
      width: 22px;
      height: 22px;
    }
  }
}

.contacts__subtitle {
  margin: 0 0 9px;
  font-weight: 600;
  font-size: 18px;
  line-height: 18px;

  @include vp-1199 {
    margin: 0 0 5px;
    font-size: 16px;
    line-height: 16px;
  }

  @include vp-767 {
    margin: 0 0 8px;
    font-size: 14px;
    line-height: 14px;
  }
}

.contacts__text {
  margin: 0;

  @include vp-1199 {
    font-size: 14px;
    line-height: 22px;
  }

  @include vp-767 {
    line-height: 20px;
  }
}

.contacts__map {
  flex-shrink: 0;
  width: 585px;
  height: 324px;
  margin: 75px 0 0;

  @include vp-1199 {
    display: flex;
    justify-content: center;
    width: 100%;
    height: 340px;
    margin: 0;
  }

  @include vp-767 {
    width: 100%;
    height: 320px;
  }
}
